@charset "UTF-8";
/* Указываем box sizing */
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Убираем внутренние отступы */
ul[class],
ol[class] {
  padding: 0;
}

/* Убираем внешние отступы */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0;
}

/* Выставляем основные настройки по-умолчанию для body */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.3;
}

/* Удаляем стандартную стилизацию для всех ul и il, у которых есть атрибут class*/
ul[class],
ol[class] {
  list-style: none;
}

/* Упрощаем работу с изображениями */
img {
  max-width: 100%;
  display: block;
}

/* Наследуем шрифты для инпутов и кнопок */
input,
button,
textarea,
select {
  font: inherit;
}

/**
 * Основные стили
 **/
html {
  font-size: 16px;
  color: #000;
}

body {
  font-family: "Roboto", "Arial", sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word;
  font-weight: 300;
}

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto;
}

.wrap {
  min-width: 320px;
  max-width: 1170px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto;
}

.centered {
  text-align: center;
  font-size: 2rem;
  line-height: 2.3125rem;
  font-weight: 400;
  margin-bottom: 24px;
}

.header__wrap {
  background: #f7f7f7;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1440px;
  margin: 0 auto;
  padding: 30px 15px;
}

.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__nav li {
  margin: 5px 15px;
}
.header__nav li a {
  color: #000;
  text-decoration: none;
  font-weight: 400;
  font-size: 1.125rem;
}
.header__nav li a:hover {
  text-decoration: underline;
}

.header__finder {
  text-transform: uppercase;
  text-decoration: none;
  color: #f7941e;
  border: solid 1px #f7941e;
  padding: 10px 15px;
  font-size: 1.125rem;
  border-radius: 5px;
  display: inline-block;
}

@media (max-width: 700px) {
  .header {
    flex-direction: column;
  }

  .header__logo {
    margin-bottom: 24px;
  }

  .header__nav {
    margin-bottom: 24px;
    flex-wrap: wrap;
    justify-content: center;
  }
}
.about {
  max-width: 1440px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-areas: "find imgright" "imgleft work ";
  font-size: 1.25rem;
  line-height: 1.75rem;
}

.about__find {
  grid-area: find;
  background: #f1f1f2;
  align-items: center;
  justify-content: center;
  display: grid;
}
.about__find div {
  max-width: 440px;
}
.about__find h1 {
  font-size: 3.5rem;
  line-height: 4.0625rem;
  margin-bottom: 45px;
  font-weight: 500;
}
.about__find p {
  margin-bottom: 26px;
}

.about__img-right {
  grid-area: imgright;
}

.about__work {
  grid-area: work;
  background: #f1f1f2;
  align-items: center;
  justify-content: center;
  display: grid;
}
.about__work div {
  max-width: 440px;
}
.about__work h2 {
  font-size: 2rem;
  line-height: 2.8125rem;
  margin-bottom: 45px;
  font-weight: 400;
}
.about__work p {
  margin-bottom: 26px;
}

.about__img-left {
  grid-area: imgleft;
}

.btn-orange {
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  background: #f7941e;
  font-size: 1.125rem;
  padding: 20px 40px;
  border-radius: 5px;
  display: inline-block;
}

.btn-transparent-orange {
  text-transform: uppercase;
  text-decoration: none;
  color: #f7941e;
  border: solid 1px #f7941e;
  padding: 20px 40px;
  font-size: 1.125rem;
  border-radius: 5px;
  display: inline-block;
}

@media (max-width: 1000px) {
  .about {
    grid-template-columns: 100%;
    grid-template-areas: "imgright" "find " "imgleft" "work ";
  }

  .about__img-left,
.about__img-right {
    background: #f1f1f2;
  }

  .about__img-left img,
.about__img-right img {
    margin: 0 auto;
  }

  .about__work,
.about__find {
    padding: 32px 20px;
  }
}
@media (max-width: 1000px) {
  .about__find h1 {
    font-size: 2.5rem;
    line-height: 3.125rem;
    margin-bottom: 24px;
  }
}
.gifts__wrap {
  padding: 95px 0;
}

.gifts > p {
  text-align: center;
  font-size: 1.125rem;
  line-height: 1.5rem;
  margin-bottom: 64px;
}

.gifts__blocks {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.gifts__items {
  max-width: 270px;
  background: #f7f7f7;
}
.gifts__items:hover {
  background: #fff;
  box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.05);
}

.gifts__items-txt {
  padding: 24px;
}
.gifts__items-txt h3 {
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 1.5rem;
  margin-bottom: 74px;
}
.gifts__items-txt p {
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 1.1875rem;
}
.gifts__items-txt p a {
  text-decoration: none;
  color: #000;
}
.gifts__items-txt p a:hover {
  text-decoration: underline;
}

@media (max-width: 800px) {
  .gifts__blocks {
    flex-wrap: wrap;
    justify-content: space-evenly;
    row-gap: 2rem;
  }
}
.signup__wrap {
  background: #f7941e;
  padding: 80px 0;
}

.signup {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.signup__txt {
  font-size: 2rem;
  line-height: 2.8125rem;
  font-weight: 300;
}
.signup__txt h2 {
  font-weight: 400;
}

.btn-black {
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  background: #000;
  font-size: 1.125rem;
  padding: 14px 25px;
  border-radius: 5px;
  display: inline-block;
  margin-right: 16px;
}

.btn-transparent-black {
  text-transform: uppercase;
  text-decoration: none;
  color: #000;
  border: solid 1px #000;
  padding: 14px 25px;
  font-size: 1.125rem;
  border-radius: 5px;
  display: inline-block;
}

@media (max-width: 900px) {
  .signup {
    flex-direction: column;
    justify-content: center;
    gap: 36px;
  }

  .signup__txt {
    text-align: center;
  }
}
@media (max-width: 500px) {
  .btn-black,
.btn-transparent-black {
    width: 100%;
    text-align: center;
  }

  .btn-black {
    margin-bottom: 20px;
  }
}
.posts__wrap {
  padding: 80px 0;
}

.posts > p {
  text-align: center;
  font-size: 1.125rem;
  line-height: 1.5rem;
  margin-bottom: 64px;
}

.posts__block {
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-areas: "postbig postbig " "post-left post-right";
}

.posts__big {
  grid-area: postbig;
  display: flex;
  background: #000;
  color: #fff;
  align-items: center;
  margin-bottom: 40px;
}

.posts__big-img {
  margin-right: 80px;
}

.posts__left {
  grid-area: post-left;
}

.posts__right {
  grid-area: post-right;
}

.posts-author {
  font-weight: 300;
  font-size: 0.875rem;
  line-height: 1.25rem;
  margin-bottom: 27px;
}

.posts__big-txt {
  max-width: 550px;
}
.posts__big-txt h2 {
  font-weight: 400;
  font-size: 3rem;
  line-height: 3.5rem;
  margin-bottom: 60px;
}

.posts__big-read {
  font-size: 1.125rem;
  color: #fff;
  line-height: 1.5625rem;
}
.posts__big-read a {
  color: #fff;
  text-decoration: none;
}
.posts__big-read a:hover {
  text-decoration: underline;
}

.posts__item {
  padding: 48px;
}
.posts__item h2 {
  font-size: 1.5rem;
  line-height: 2.0625rem;
  font-weight: 500;
  margin-bottom: 16px;
}
.posts__item p {
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 1.5625rem;
  margin-bottom: 16px;
}
.posts__item:hover {
  box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.05);
}

.posts__item-read a {
  color: #000;
  text-decoration: none;
  font-size: 0.875rem;
  line-height: 1.25rem;
}
.posts__item-read a:hover {
  text-decoration: underline;
}

@media (max-width: 860px) {
  .posts__block {
    grid-template-columns: 100%;
    grid-template-areas: "postbig  " "post-left" "post-right";
  }

  .posts__big {
    margin-bottom: 20px;
  }

  .posts-author {
    margin-bottom: 20px;
  }

  .posts__big-txt {
    padding-right: 15px;
  }
  .posts__big-txt h2 {
    font-weight: 300;
    font-size: 2rem;
    line-height: 2.6875rem;
    margin-bottom: 25px;
  }

  .posts__big-img {
    margin-right: 40px;
  }
}
@media (max-width: 600px) {
  .posts__big {
    flex-direction: column;
    padding: 30px 0;
  }

  .posts__big-img {
    margin: 0 auto 20px auto;
  }

  .posts__big-txt {
    max-width: 400px;
    padding: 0 15px;
  }

  .posts__item {
    padding: 25px 15px;
  }
}
.footer__wrap {
  background: #f7f7f7;
  padding: 100px 0 80px 0;
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__nav {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.footer__nav li {
  margin: 5px 25px;
}
.footer__nav a {
  text-decoration: none;
  color: #000;
  font-size: 0.875rem;
}
.footer__nav a:hover {
  text-decoration: underline;
}

.footer__hr {
  margin: 30px 0;
  border-bottom: 1px solid #000;
  width: 80%;
}

.footer__copy {
  font-size: 0.875rem;
  line-height: 1.25rem;
}